import React, { Component } from 'react';
import UpcomingCard from "./UpcomingCard";

class UpcomingView extends Component {

  render() {
    return (
      <div>
        <div className="heading-div">
          <h2 className="heading-float-left">Upcoming</h2>
          <div className="heading-float-right">
            <img className="btn-outline-secondary glyph-icon m-1" alt="addEvent" src="assets/si-glyph-circle-plus.svg" data-toggle="modal" data-target="#upcomingModal"/>
          </div>
        </div>
        <div className="non-header">
          <div className="heading-div">
            {this.handleUpcoming()}
          </div>
        </div>
      </div>
    );
  }

  handleUpcoming = () => {
    //GO TO APP STATE VARIABLE AND GRAB ALL UPCOMING ITEMS
    const upcoming = this.props.appState.upcoming;
    const listUpcoming = [];

    //LOOK THROUGH EACH ITEM
    for (let i = 0; i < upcoming.length; i++) {
      listUpcoming.push(<UpcomingCard config={upcoming[i]} onComplete={() => { this.props.onComplete(i) }} key={"Upcoming " + i}/>);
    }

    if (listUpcoming.length === 0) {
      return "All Tasks Completed.";
    }

    //CHECK IF THE DATE IT'S DUE IS TODAY

    //IF SO, CREATE AN UPCOMINGCARD AND APPEND IT TO A BLOCK OF UPCOMINGCARDS
    //GIVE IT THE PARAMETERS

    return listUpcoming; //RETURN THE BLOCK OF UPCOMINGCARDS
  }

}

export default UpcomingView;
